* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "roboto", sans-serif;
  background-image: url("https://miro.medium.com/max/700/1*rv6izaVtMrqc7-AHf8-YAg.jpeg");
  background-size: cover;
}

a {
  color: #60bf70 !important;
  text-decoration: none;
  padding: 20px;
  margin-top: 20px;
}

a:hover {
  color: black !important;
}

a.active {
  color: #57d96d !important;
}
